<template>
  <transition name='fade'>
    <div class='myClubPage'>
      <v-Header class="head">
        <span slot='text'>我的俱乐部</span>
      </v-Header>
      <div id='listsScrollCon' class='listsScrollCon'>
      <nav class='navbar'>
        <div :class="['item',type=='myClub'&&'active']" @click='changeType("myClub")'>我管理的俱乐部（{{myclubList.length}}）</div>
        <div :class="['item',type!=='myClub'&&'active']"  @click='changeType("joinClub")'>我加入的俱乐部（{{joinClubList.length}}）</div>
        <div :class="['line',type=='myClub'&&'active']"></div>
      </nav>
      <div :class="['myclub',type=='myClub'&&'active']">
        <div class='clubCon'>
          <div class='clubCon' v-if='myclubList.length>0'>
            <div v-for='info in myclubList' :key='info.clubId' @click="gotoPath(info)">
              <mt-cell class="list" :title="info.name" is-link>
                  <img slot="icon" :src="info.headimg" class='userHeader'>
              </mt-cell>
            </div>
          </div>
        </div>
        <div class='publishBtn' v-if='(_userType==1&&!_auditUserType)||_userType==4' @click="createClub">创建俱乐部</div>
      </div>
      <div :class="['joinclub',type!='myClub'&&'active']">
        <div class='clubCon' v-if='joinClubList.length>0'>
          <div v-for='info in joinClubList' :key='info.clubId' @click="gotoPath(info)">
            <mt-cell class="list" :title="info.name" is-link >
                <img slot="icon" :src="info.headimg" class='userHeader'>
            </mt-cell>
          </div>
        </div>
      </div>
      </div>
    </div>
  </transition>
</template>
<script>
  import Header from '../../components/header';
  import {mapState} from 'vuex';
  export default {
      name: '',
      components: {'v-Header':Header},
      props: [],
      data () {
        return {
          type:'myClub',
          myclubList:[],
          joinClubList:[]
        }
      },
      watch: {},
      computed: {
        ...mapState({
          _userType:state => state.userInfo.userType,//当前用户id
          _auditUserType:state => state.userInfo.auditUserType,
        }),
      },
      created () {
        this.selected=2;
        this.$http.get(this.$port.MY_CLUB,{params:{auditStatus:0}}).then((response) => {
          const {data} = response;
          if(response.code == 200){
            this.myclubList=this.myclubList.concat(data);
          }
        })
        .catch((error) => {
          console.log(error.response.data)
        });
        this.$http.get(this.$port.MY_CLUB_JOIN,{params:{}}).then((response) => {
          const {data} = response;
          if(response.code == 200){
            this.joinClubList=this.joinClubList.concat(data);
          }
        })
        .catch((error) => {
          console.log(error.response.data);
        });
      },
      methods: {
        changeType (data) {
          this.type=data;
        },
        gotoPath(_obj){
          if(_obj.auditStatus){
            this.$router.push({path:'/auth/club',query:{clubId:_obj.clubId}});
          }else{
            this.$router.push({path:'/detail/club',query:{clubId:_obj.clubId}});
          }
        },
        createClub(){
          this.$router.push({path:'/auth/club'});
        }
      }
    }
</script>
<style lang="scss" scoped>
*{
  border: none;
  margin: 0;
  padding: 0;
}
.list{
  height: 1rem;
  line-height: 1rem;
  font-size: 0.32rem;
  color: #333333;}
.myClubPage{
  width:100%;
  height:100%;
  position: absolute;
  top:0;
  left:0;
  overflow: hidden;
  background:white;
  .listsScrollCon{
    position: absolute;
    top:0.82rem;
    left:0;
    right:0;
    bottom:0;
    margin:auto;
    overflow: hidden;
    .navbar{
      position:relative;
      width:7.5rem;
      height:0.86rem;
      display:flex;

      .item{
        flex:1;
        font-size:0.32rem;
        line-height:0.86rem;
        text-align:center;
        color:#666666;
        &.active{
          color:#1797c6;
        }
      }
      .line{
        width: 50%;
        position: absolute;
        left: 0;
        bottom: 0;
        border-bottom: solid #1797c6 1px;
        transform:translate(100%,0);
        transition:transform 0.3s;
        &.active{
          transform:translate(0%,0);
        }
      }
    }
  }
  .myclub,.joinclub{
    position: absolute;
    top:0.87rem;
    left:0;
    right:0;
    bottom:0;
    margin:auto;
    overflow: hidden;
  }
  .joinclub{
    transform:translate(100%,0);
    transition:transform 0.3s;
    &.active{
      transform:translate(0%,0);
    }
  }
  .myclub{
    transform:translate(-100%,0);
    transition:transform 0.3s;
    &.active{
      transform:translate(0%,0);
    }
    .clubCon{
      bottom:0.88rem;

    }
  }
  .userHeader{
        width:0.76rem;
        height:0.76rem;
        border-radius: 0.76rem;
        margin:0 0.4rem;
      }
    .clubCon{
      position: absolute;
      top:0;
      left:0;
      right:0;
      bottom:0;
      margin:auto;
      overflow-x: hidden;
      overflow-y: scroll;
      -webkit-overflow-scrolling:touch;

    }

  .publishBtn{
      width:100%;
      height:0.88rem;
      position:absolute;
      bottom:0;
      left:0;
      background:url('../../assets/images/publishBtn.jpg') no-repeat;
      background-size:cover;
      color:white;
      line-height:0.88rem;
      font-size:0.36rem;
      text-align:center;
    }
}
</style>



